import React from 'react';
import styles from './team.css';
import Headline from '../headline/headline';
import Paragraph from '../paragraph/paragraph';
import config from '../../config';

const TeamMember = ({ role, img, name, children }) => (
  <div className={styles.member}>
    <div
      className={styles.memberImg}
      style={{ backgroundImage: `url(${img}` }}
    />
    <div>
      <Headline level={3} className={styles.memberName}>
        {name}
      </Headline>
      <span className={styles.role}>{role}</span>
      <Paragraph className={styles.memberDescription}>{children}</Paragraph>
    </div>
  </div>
);

export default function Team({ data }) {
  return (
    <div className={styles.team}>
      {data.map((i) => (
        <TeamMember role={i.role} key={i.name} img={`${i.img}`} name={i.name}>
          {i.description}
        </TeamMember>
      ))}
    </div>
  );
}
